import React from 'react'
import styles from './Socials.module.css'

import { socials } from '../../data/socials'

export default function Socials (props) {
  const onClickSocialHandler = (social, e) => {
    e.stopPropagation();

    window.open(
      social.hash,
      null,
      'height=400,width=600,status=yes,toolbar=no,menubar=no,location=no'
    );
  }

  return (
    <div className={styles.socials}>
        {
          socials.map(social => {
            return <button className={styles.socialItem} key={social.name} onClick={onClickSocialHandler.bind(this, social)}>
              <div className={styles.socialItemIcon}>
                { social.icon }
              </div>
            </button>
          })
        }
    </div>
  )
}