/* eslint camelcase: 0 */

/*
 * Модуль инициализации счетчиков
 *
 */

const
  /*
   * Свойства
   *
   */

  // Протокол
  protocol = 'https:',

  // Массив счетчиков
  counters = [

    // Мейл.ру
    {
      name: 'mailruCounter',
      targetId: '#mailruCounter',

      init: (elementId) => {

        var

          _tmr = null, // Объект таймера

          $a = null, // ссылка на картинке счетчика
          $img = null; // Картинка счетчика

        // Настраиваем счетчик
        _tmr = window._tmr || (window._tmr = []);

        // Отправляем счетчик просмотра страницы
        _tmr.push({id: '11659', type: 'pageView', start: (new Date()).getTime()});

        (function(d, w, id) {

          var ts = null,
            f = null;

          if (d.getElementById(id)) {

            return;

          }

          ts = d.createElement('script');

          ts.type = 'text/javascript';
          ts.async = true; ts.id = id;
          ts.src = protocol + '//top-fwz1.mail.ru/js/code.js';

          f = function() {

            var s = d.getElementsByTagName('script')[0];

            s.parentNode.insertBefore(ts, s);

          };

          if (w.opera === '[object Opera]') {

            d.addEventListener('DOMContentLoaded', f, false);

          } else {

            f();

          }

        })(document, window, 'topmailru-code');

        /*
         * Настраиваем блок счетчика
         *
         */

        $a = document.createElement('a');
        $a.setAttribute('target', '_blank');
        $a.setAttribute('href', protocol + '//top.mail.ru/jump?from=11659');

        $img = document.createElement('img');
        $img.setAttribute('src', protocol + '//top-fwz1.mail.ru/counter?id=11659;t=409;l=1');
        $img.setAttribute('alt', 'Рейтинг@Mail.ru');
        $img.setAttribute('width', '88px');
        $img.setAttribute('height', '31px');
        $img.setAttribute('border', '0');

        // Добавляем картинку счетчика в элемент
        document.querySelector(elementId).appendChild($a.appendChild($img));

      }
    },

    // tnsCounter
    {
      name: 'tnsCounter',
      targetId: '#tnsCounter',

      init: (elementId) => {

        var $img = null,
          src = null;

        // Настраиваем блок счетчика
        src = protocol + '//www.tns-counter.ru/V13a***R>' + document.referrer.replace(/\*/g, '%2a') + '*rg_ru/ru/UTF-8/tmsec=rg_total/';

        $img = document.createElement('img');
        $img.setAttribute('src', src);
        $img.setAttribute('border', '0');
        $img.setAttribute('width', '1px');
        $img.setAttribute('height', '1px');
        $img.setAttribute('style', 'position: absolute; left: -10000px;');

        // Вставляем в контейнер
        document.querySelector(elementId).appendChild($img);

      }
    },

    // Яндекс метрика
    {

      name: 'yaMetrikaCounter',
      targetId: '#yaMetrika',

      init: () => {

        var counterId = '22322746';

        /* eslint-disable */

        // Обрати внимание, я туда засунул counterId
        (function (m, e, t, r, i, k, a) { m[i] = m[i] || function () { (m[i].a = m[i].a || []).push(arguments) }; m[i].l = 1 * new Date(); k = e.createElement(t), a = e.getElementsByTagName(t)[0], k.async = 1, k.src = r, a.parentNode.insertBefore(k, a) })(window, document, "script", "https://mc.yandex.ru/metrika/tag.js", "ym"); ym(counterId, "init", { clickmap: true, trackLinks: true, accurateTrackBounce: true, webvisor: true, trackHash: true });

      }
    },

  ],

  // Инициализация счетчиков
  initCounters = () => {

    // Инициализируем все счетчики
    counters.forEach(function(counter) {

      counter.init(counter.targetId);

    });

  },

  /*
   * Инициализация подписчиков модуля
   *
   */
  init = () => {

    initCounters();

  };

// Экспортируем модуль
export const Counters = {
  init
};
