import React from 'react'
import styles from './PersonInfo.module.css'

import { ReactComponent as Rhombus } from '../../assets/svg/rhombus.svg'

export default function PersonInfo(props) {
  return (
    <div className={styles.root}>
      <div className={styles.personInfoHeader}>
        <div className={styles.personInfoCaption}>
          <div className={styles.personInfoTitle}>
            <div className={`${styles.personInfoName} ${props.person.surname.length > 10 ? styles.personInfoSurnameMiddle : ''}`}>{props.person.name} {props.person.patronymic} </div>
            <div className={`${styles.personInfoSurname} ${props.person.surname.length > 10 ? styles.personInfoSurnameMiddle : ''}`}> {props.person.surname}</div>
          </div>
          <div className={styles.personInfoPosition} dangerouslySetInnerHTML={{
            __html: props.person.position
          }}/>
          <div className={styles.lineBox}>
            <div className={styles.lineBoxDot}>
              <Rhombus/>
            </div>
          </div>
        </div>
        <div className={styles.personInfoDescription} dangerouslySetInnerHTML={{
          __html: props.person.description
        }}/>
        <div className={styles.personInfoArticle}>
          <div className={styles.personInfoArticleTitle}>
            {props.person.article.title}
          </div>
          <div className={styles.personInfoArticleContent} dangerouslySetInnerHTML={{
            __html: props.person.article.content
          }}/>
        </div>
      </div>
    </div>
  )
}