import React from 'react'
import styles from './Health.module.css'

import { ReactComponent as Rhombus } from '../../assets/svg/rhombus.svg'

export default function Health(props) {
  return (
    <div className={styles.root}>
      <div className={styles.article}>
        <div className={styles.rhombusBox}>
          <div className={styles.rhombusBoxLeftLine}/>
          <div className={styles.rhombusBoxRhombus}>
            <Rhombus/>
          </div>
          <div className={styles.rhombusBoxRightLine}/>
        </div>
        <div className={styles.title}>Золотая маска<br/> Краснопольской</div>
        <div className={styles.subTitle}>Накануне 30-летия "Российской газеты" мы говорим спасибо всем, кто лечит и спасает</div>
        <p>
          В "Российской газете" работает удивительный журналист, хедлайнер для любой книги рекордов. Подвиг не только в авторитетных летах медицинского обозревателя, для которого и сегодня собраться в Чечню или Чикаго, Конаково или Краснодар - не вопрос. Даже не в уникальной памяти и дружбе с докторами, которые с годами стали мировыми светилами, а для нее все они Миши, Саши, Толики, Лелики... Не было случая, чтобы хоть кто-то из них не ответил на звонок Краснопольской. Но она никогда (!) не бывает заложником приятельства, оставаясь камертоном чести в профессиональном сообществе, где она давно самая своя. Часто пишет и говорит о том, чем больна наша медицина и как надо ее лечить, чтобы выздоровел пациент не только в Склифе, но и на бамовской станции Тында... И помогает каждодневно и ближним, и дальним людям, умудряется взяться за самый трудный случай и вместе с докторами совершить чудо. Как она влюбляется в маленьких спасенных людей, которые становятся практически ее родней...
        </p>
        <p>
          Главный же цимес в том, что на этой медийной поляне Краснопольской нет равных. Скажите, кто еще из наших коллег, без записи на диктофон, может по ходу интервью спорить с собеседником-академиком, набирать текст на компьютере и тут же, отсекая лишнее, визировать и отправлять в печать?
        </p>
        <p>
          Даже врачи уверены, что она доктор по образованию, хотя - нет, филолог, всего лишь дочь медиков. И кто из них не мечтает побывать на знаменитой кухне Краснопольской на Кутузовском проспекте или принять участие в дискуссионном совете экспертов, который ведет <a href="https://rg.ru/sujet/4899" target="_blank" rel="noopener noreferrer">Краснопольская</a> в "Российской газете"?
        </p>
        <p>
          Диагноз от Краснопольской дорогого стоит. Она знает цену авторитетам реальным и рекламным. Замеченный справедливой Краснопольской - как отмеченный свыше. В апреле на полосе "Медицина" вышло большое интервью с Владимиром Порхановым, главврачом Краснодарской краевой больницы, потом никто и не удивился, что он вместе с Галиной Волчек стал Героем Труда России.
        </p>
        <p>
          Хотелось бы написать, какой ее день рождения мы празднуем - это такая духоподъемная дата! Но Ирине Григорьевне, блистательной женщине, это может и не понравиться.
        </p>
        <p>
          Поэтому главное - мы очень любим Ирину Григорьевну и лучшую про нее шутку: если только приложить статью Краснопольской к больному месту, то человек уже начинает выздоравливать.
        </p>
        <p>
          Попасть в руки такого журналиста, мне кажется, то же самое, что на прием к великому врачу.
        </p>
        <p>
          Есть театральный фестиваль и престижнейшая театральная премия - "Золотая маска". "Российская газета" решила отметить Золотой маской Краснопольской героев полосы "Медицина" этих дней.
        </p>
        <p>
          Кто, как не они, сегодня заслуживают наши аплодисменты?
        </p>

        <div className={styles.info}>
          <div className={styles.infoPerson}>
            <div className={styles.infoPersonTitle}>
              <span className={styles.infoPersonName}>Алексей</span>
              <span className={styles.infoPersonSurname}>Шабунин</span>
            </div>
            <div className={styles.infoPersonDescription}>
              главный врач больницы им. С.П. Боткина, член-корреспондент РАН
            </div>
            <div className={styles.infoPersonPhoto}>
              <img className={styles.infoPersonPhotoImage} src={'https://cdnimg.rg.ru/pril/article/190/39/65/Shabunin_1000x667.JPG'} alt="Алексей Шабунин. Фото: Алексанадр Корольков/РГ"/>
              <span className={styles.infoPersonPhotoDescription}>Алексей Шабунин. Фото: Алексанадр Корольков/РГ</span>
            </div>
          </div>

          <p>
            - При первых симптомах сдал тесты. Получил положительный результат. Сразу самоизолировался в своем рабочем кабинете. Продолжаю работать. С коллективом общаюсь исключительно в режиме видеоконференций. После переливания плазмы чувствую себя гораздо лучше. Иду на поправку. А наша Боткинская, как всегда, в круглосуточном режиме. Экстренную помощь никто не отменял. Помогаем зараженным коронавирусом онкобольным и пациентам с заболеваниями крови.
          </p>
          <p>
            Боткинская сегодня - это все направления современной медицины. От операции с помощью робота да Винчи до трансплантации органов. Это база 24 кафедр четырех московских медицинских вузов. У нас трудятся пять членов Российской академии наук, ваш покорный слуга в том числе, более 100 профессоров, более 200 кандидатов наук. Мы лечим 104 тысячи стационарных больных в год и миллион амбулаторных пациентов.
          </p>
        </div>
        <div className={styles.info}>
          <div className={styles.infoPerson}>
            <div className={styles.infoPersonTitle}>
              <span className={styles.infoPersonName}>Николай</span>
              <span className={styles.infoPersonSurname}>Коновалов</span>
            </div>
            <div className={styles.infoPersonDescription}>
              нейрохирург, заместитель директора НМИЦ нейрохирургии имени академика Н. Н. Бурденко, член-корреспондент РАН
            </div>
            <div className={styles.infoPersonPhoto}>
              <img className={styles.infoPersonPhotoImage} src={'https://cdnimg.rg.ru/pril/article/190/39/65/Konovalov_1000x667.JPG'} alt="Николай Коновалов. Фото: Из личного архива"/>
              <span className={styles.infoPersonPhotoDescription}>Николай Коновалов. Фото: Из личного архива</span>
            </div>
          </div>

          <p>
            - Данных о том, что коронавирус поражает позвоночник, практически пока нет. А вот о том, что он поражает центральную нервную систему есть.
          </p>
          <p>
            К сожалению, я не могу сказать, что у здоровых людей с возрастом не наступят некие изменения в позвоночнике. Говорить, что такое происходит со всеми, неправильно. Но вероятность высокая. Очень важна генетическая предпосылка. Если мы проведем МРТ людям в возрасте шестидесяти лет, то у одних вовсе не будет никаких изменений. У других они будут настолько выражены, что могут привести к глубокой инвалидности. Факторы риска развития тут и образ жизни, и склонность к стрессам, и занятия спортом, и лишний вес. Даже у занимающихся физической культурой, следящих за своим здоровьем могут развиваться выраженные изменения не только в пожилом, но и в молодом возрасте.
          </p>
        </div>
        <div className={styles.info}>
          <div className={styles.infoPerson}>
            <div className={styles.infoPersonTitle}>
              <span className={styles.infoPersonName}>Николай</span>
              <span className={styles.infoPersonSurname}>Плавунов</span>
            </div>
            <div className={styles.infoPersonDescription}>
              главный врач московской скорой и неотложной медицинской помощи, профессор
            </div>
            <div className={styles.infoPersonPhoto}>
              <img className={styles.infoPersonPhotoImage} src={'https://cdnimg.rg.ru/pril/article/190/39/65/Plavunov_1000x667.JPG'} alt="Николай Плавунов. Фото: Сергей Куксин/ РГ"/>
              <span className={styles.infoPersonPhotoDescription}>Николай Плавунов. Фото: Сергей Куксин/ РГ</span>
            </div>
          </div>

          <p>
            - Вот уже сто лет подряд достаточно набрать короткий номер: раньше - 03, теперь - 103, и человек абсолютно уверен: помощь придет. "Скорая помощь" - команда единомышленников. Если такой команды нет, то нет и самой службы, потому что у нас особая специфика. Член такой команды не должен смотреть на часы, не должен знать слова "нет", когда его призывают на помощь. В московской команде 11 тысяч человек. В среднем каждый день мы выполняем 12 тысяч вызовов. На улицы Москвы выходит 1040 бригад "скорой". Это в среднем. Когда нет ЧП. Но, к сожалению, без экстрима не обойтись в любом городе, тем более, в таком многомиллионном, как Москва.
          </p>
          <p>
            Каждый год мы выполняем 4 миллиона вызовов. Вас удивляет, что "скорая" прибывает через считаные минуты. В этом нет тайны. У нас есть подстанции. Их сейчас 60. Они практически в шаговой доступности. И потому не опаздывают. Мы работаем по расчетному времени прибытия. Информационная система позволяет знать местоположение каждой бригады в любую минуту. И на вызов направляется ближайшая к пациенту.
          </p>
        </div>
        <div className={styles.info}>
          <div className={styles.infoPerson}>
            <div className={styles.infoPersonTitle}>
              <span className={styles.infoPersonName}>Владимир</span>
              <span className={styles.infoPersonSurname}>Порханов</span>
            </div>
            <div className={styles.infoPersonDescription}>
              академик РАН, Герой Труда, главный врач Краснодарской краевой больницы № 1 имени Очаповского
            </div>
            <div className={styles.infoPersonPhoto}>
              <img className={styles.infoPersonPhotoImage} src={'https://cdnimg.rg.ru/pril/article/190/39/65/Porkhanov_1000x667.JPG'} alt="Владимир Порханов. Фото: Геннадий Аносов/РГ"/>
              <span className={styles.infoPersonPhotoDescription}>Владимир Порханов. Фото: Геннадий Аносов/РГ</span>
            </div>
          </div>

          <p>
            - Болезни умудряются не в единственном количестве поражать одного человека. И тогда, к примеру, возникает необходимость удаления рака легкого у больного с ишемической болезнью сердца. Ранее таких пациентов вовсе не оперировали: был крайне высок риск осложнений со стороны сердечно-сосудистой системы. Сейчас у них есть шанс на жизнь.
          </p>
          <p>
            У одного операционного стола собирается сплоченная мультидисциплинарная команда хирургов. Такие комбинированные операции почти всегда уникальны: проводятся после тщательного планирования, подготовки. Они требуют от хирургов высокого мастерства и ювелирной точности.
          </p>
        </div>
        <div className={styles.info}>
          <div className={styles.infoPerson}>
            <div className={styles.infoPersonTitle}>
              <span className={styles.infoPersonName}>Анна</span>
              <span className={styles.infoPersonSurname}>Попова</span>
            </div>
            <div className={styles.infoPersonDescription}>
              глава Роспотребнадзора, профессор
            </div>
            <div className={styles.infoPersonPhoto}>
              <img className={styles.infoPersonPhotoImage} src={'https://cdnimg.rg.ru/pril/article/190/39/65/Popova_1000x667.JPG'} alt="Анна Попова. Фото: Сергей Михеев/РГ"/>
              <span className={styles.infoPersonPhotoDescription}>Анна Попова. Фото: Сергей Михеев/РГ</span>
            </div>
          </div>

          <p>
            - Россияне, планирующие отпуск этим летом, должны исходить из того, что риск коронавируса остается. Но нельзя забывать, что остаются и другие вирусные инфекции. Одна из самых высокозаразных, известных человеку, - корь. В первый день сыпь появляется на лице и шее. На второй - покрывает туловище. На третий - руки и ноги. А еще кашель, конъюнктивит. Но все это через несколько дней проходит. И не в них опасность кори. А в том, что она может вызвать серьезные осложнения. Вплоть до летального исхода.
          </p>
          <p>
            Кто в группе риска? Любой человек, не имеющий иммунитета к вирусу кори. Тот, кто не прошел противокоревую вакцинацию, не переболел корью. Кроме того, в группу риска входят работники медицинских учреждений, образовательных организаций, торговли, мигранты, кочующие группы населения. Источник заражения - только больной корью человек.
          </p>
        </div>
        <div className={styles.info}>
          <div className={styles.infoPerson}>
            <div className={styles.infoPersonTitle}>
              <span className={styles.infoPersonName}>Григорий</span>
              <span className={styles.infoPersonSurname}>Ройтберг</span>
            </div>
            <div className={styles.infoPersonDescription}>
              академик РАН, руководитель клиники АО "Медицина"
            </div>
            <div className={styles.infoPersonPhoto}>
              <img className={styles.infoPersonPhotoImage} src={'https://cdnimg.rg.ru/pril/article/190/39/65/Roytberg_01_1000x667.JPG'} alt="Григорий Ройтберг. Фото: Сергей Куксин/РГ"/>
              <span className={styles.infoPersonPhotoDescription}>Григорий Ройтберг. Фото: Сергей Куксин/РГ</span>
            </div>
          </div>

          <p>
            - Идет война. Да, без выстрелов. И спасают нас в этой войне медики. Значит, мы, причем не только организаторы здравоохранения, должны спасти медиков. Спасая их, мы спасаем наших пациентов. Медики приняли эпидемический удар на себя. Приняли как само собой разумеющееся: а как иначе? Им не предписано сидеть дома, не выходить на улицу и так далее. Напротив, они обязаны, они должны.
          </p>
          <p>
            Спасение в экстремальной ситуации - дело врачей. И, как сказал наш президент, "медики проявляют удивительную самоотдачу. Медиков надо поддержать". Так хочется, чтобы это пожелание распространялось не только на время эпидемии. В Италии люди выходят на балконы и аплодируют врачам. Жду ли аплодисментов у нас? Жду цивилизованного отношения к медикам. Оно же было в России. Традиционно к врачеванию относились с пиететом.
          </p>
        </div>
        <div className={styles.info}>
          <div className={styles.infoPerson}>
            <div className={styles.infoPersonTitle}>
              <span className={styles.infoPersonName}>Сергей</span>
              <span className={styles.infoPersonSurname}>Петриков</span>
            </div>
            <div className={styles.infoPersonDescription}>
              директор НИИ скорой помощи им. Н. В. Склифосовского, член-корреспондент РАН
            </div>
            <div className={styles.infoPersonPhoto}>
              <img className={styles.infoPersonPhotoImage} src={'https://cdnimg.rg.ru/pril/article/190/39/65/Petrikov_1000x667.JPG'} alt="Сергей Петриков. Фото: Сергей Михеев/РГ"/>
              <span className={styles.infoPersonPhotoDescription}>Сергей Петриков. Фото: Сергей Михеев/РГ</span>
            </div>
          </div>

          <p>
            - В нашем институте, в центре радиохирургии, проведена двухсотая операция с помощью гамма-ножа. Гамма-нож спасает за один день. Лишь иногда, когда требуется повторное облучение гамма-ножом, пациент приходит на повторный сеанс. Надо заметить, что гамма-нож абсолютно не похож на наши представления об обычном ноже. Гамма-нож - это целая установка. В ее составе сложнейшая современная техника, которую человек сделал безопасной и очень эффективной для спасения от тяжелейших заболеваний мозга. Операции с помощью гамма-ножа проводятся не инвазивно, то есть без скальпеля, под местным обезболиванием. Пациенту не требуется стационарное лечение.
          </p>
          <p>
            Часто спрашивают: все-таки облучение?! Мы же боимся даже самого этого слова. При гамма-ноже оно не опасно для человека? Тем более если направлено в главный орган - мозг? Гамма-нож устроен таким образом, что при помощи тонкой компьютерной настройки его лучи направляются точно на опухоль. Они разрушают ДНК больных клеток, не задевая при этом здоровые.
          </p>
        </div>
        <div className={styles.info}>
          <div className={styles.infoPerson}>
            <div className={styles.infoPersonTitle}>
              <span className={styles.infoPersonName}>Елена</span>
              <span className={styles.infoPersonSurname}>Новожилова</span>
            </div>
            <div className={styles.infoPersonDescription}>
              доктор медицинских наук, заведующая отделением "опухоли головы-шеи" московской онкологической больницы № 62
            </div>
            <div className={styles.infoPersonPhoto}>
              <img className={styles.infoPersonPhotoImage} src={'https://cdnimg.rg.ru/pril/article/190/39/65/Novozhilova_1000x667.JPG'} alt="Елена Новожилова. Фото: Сергей Куксин/РГ"/>
              <span className={styles.infoPersonPhotoDescription}>Елена Новожилова. Фото: Сергей Куксин/РГ</span>
            </div>
          </div>

          <p>
            - Очень хорошо помню свою первую операцию. На операционном столе была женщина, страдающая раком щитовидной железы. И хотя до этого я множество раз ассистировала при таких операциях… Нет, руки не дрожали. Но было страшновато... Сколько лет ни работаю, точно могу сказать: нет одинаковых людей. Все операции очень индивидуальны. Человек не может быть "случаем" - это всегда личность с индивидуальными чертами...
          </p>
          <p>
            Многократно план операции проигрывался в голове. Но когда я начала операцию, страх и неуверенность прошли. С той поры у меня привычка: каждую предстоящую операцию мысленно проигрываю. Хирург должен быть уверен в себе? Уверен - да! Но ни в коем случае не самоуверен.
          </p>
        </div>
        <div className={styles.videoBox}>
          <video className={styles.videoBoxVideo} src={'https://cdnimg.rg.ru/video/content/0/65/23/RG_Zhit_zdorovo!_Vypusk_ot_27.04.2020.mp4'} controls={'controls'}/>
          <div className={styles.videoBoxDescription}>Автор: Первый канал</div>
        </div>
        <div className={styles.info}>
          <div className={styles.infoPerson}>
            <div className={styles.infoPersonTitle}>
              <span className={styles.infoPersonName}>Андрей</span>
              <span className={styles.infoPersonSurname}>Каприн</span>
            </div>
            <div className={styles.infoPersonDescription}>
              академик РАН, главный онколог Минздрава РФ
            </div>
            <div className={styles.infoPersonPhoto}>
              <img className={styles.infoPersonPhotoImage} src={'https://cdnimg.rg.ru/pril/article/190/39/65/4345879.JPG'} alt="Андрей Каприн. Фото: Аркадий Колыбалов/РГ"/>
              <span className={styles.infoPersonPhotoDescription}>Андрей Каприн. Фото: Аркадий Колыбалов/РГ</span>
            </div>
          </div>

          <p>
            - Пандемия внесла свои коррективы в работу лечебных учреждений всей страны. Филиал нашего Центра - НИИ урологии и интервенционной радиологии имени Лопаткина - оказался в числе перепрофилированных. И с 24 апреля на 156 койках принимает пациентов с подозрением или подтвержденным диагнозом новой коронавирусной инфекции.
          </p>
          <p>
            Оперируем гораздо больше, чем обычно. Мы понимаем, что рак не уходит на карантин. Не уходят и другие болезни. И мы прикладываем все усилия, чтобы продолжать свою работу по основному профилю и на прежнем уровне.
          </p>
          <p>
            Филиал оказывает помощь в том числе и онкологическим пациентам, которые, увы, заразились COVID-19. Такие пациенты под наблюдением в НИИ урологии и интервенционной радиологии имени Лопаткина. В пандемию работают все наши подразделения и в Москве, и в Обнинске.
          </p>
        </div>
        <div className={styles.info}>
          <div className={styles.infoPerson}>
            <div className={styles.infoPersonTitle}>
              <span className={styles.infoPersonName}>Виктор</span>
              <span className={styles.infoPersonSurname}>Малеев</span>
            </div>
            <div className={styles.infoPersonDescription}>
              академик РАН, инфекционист
            </div>
            <div className={styles.infoPersonPhoto}>
              <img className={styles.infoPersonPhotoImage} src={'https://cdnimg.rg.ru/pril/article/190/39/65/Maleev_1000x667.JPG'} alt="Виктор Малеев. Фото: Николай Галкин/ТАСС"/>
              <span className={styles.infoPersonPhotoDescription}>Виктор Малеев. Фото: Николай Галкин/ТАСС</span>
            </div>
          </div>

          <p>
            - Среди важнейших уроков пандемии - цивилизованное отношение к вакцинации. Прежде всего она напомнила всем: никогда не надо забывать о том, что в мире всегда были и будут существовать инфекции. Они составная часть природы. Они возникают и существуют по своим собственным законам. Но, как правило, это всегда бывает внезапно и застает человечество врасплох. Так случилось и сейчас. Очень хочется думать, что теперь станет по-другому: внимание к инфекциям возрастет, и мы будем к ним более подготовлены.
          </p>
          <p>
            На сей раз, к сожалению, все страны были готовы не в полной мере. Но многое зависело от способности быстро мобилизоваться. В России удалось сохранить централизованную эпидемиологическую службу. Она смогла в короткий период решить задачи по разработке современных методов диагностики, организации эпиднадзора, мобилизации всех систем здравоохранения и социальных служб.
          </p>
        </div>
      </div>
      <div className={styles.previewImage} style={{backgroundImage: 'url(//cdnimg.rg.ru/img/content/190/39/65/10770702.jpg)'}}>

      </div>
    </div>
  )
}
