import React, {useRef, useEffect, useState} from 'react'
import MultiCarousel from 'react-multi-carousel'
import 'react-multi-carousel/lib/styles.css'
import styles from './Carousel.module.css'

import { ReactComponent as Download } from '../../assets/svg/download.svg'
import { ReactComponent as ArrowPrev } from '../../assets/svg/arrow_prev.svg'
import { ReactComponent as ArrowNext } from '../../assets/svg/arrow_next.svg'


import {items} from '../../data/lib'

const responsive = {
  superLargeDesktop: {
    breakpoint: { max: 3000, min: 1280 },
    items: 3,
    slidesToSlide: 1
  },
  desktop: {
    breakpoint: { max: 1280, min: 1024 },
    items: 3,
    slidesToSlide: 1
  },
  tablet: {
    breakpoint: { max: 1023, min: 768 },
    items: 3,
    slidesToSlide: 1
  },
  mobile: {
    breakpoint: { max: 768, min: 0 },
    items: 3,
    slidesToSlide: 1,
  }
};


export default function Carousel() {
  const carouselRef = useRef()

  const [isMoving, setIsMoving] = useState(false)

  useEffect(() => {
    if (carouselRef.current) {

      carouselRef.current.goToSlide(3)
    }
  }, [])

  return (
    <div className={styles.root}>
      <MultiCarousel
        ref={carouselRef}
        responsive={responsive}
        infinite={true}
        arrows={true}
        customLeftArrow={<div className={`${styles.arrow} ${styles.arrowPrev}`}><ArrowPrev/></div>}
        customRightArrow={<div className={`${styles.arrow} ${styles.arrowNext}`}><ArrowNext/></div>}
        beforeChange={(slide, ref2) => {
          setIsMoving(true)
          let elements = carouselRef.current.listRef.current.querySelectorAll('.react-multi-carousel-item')

          elements.forEach(item => {
            item.classList.remove(styles.itemCurrent)
            item.classList.remove(styles.itemCurrentPrev)
            item.classList.remove(styles.itemCurrentNext)
          })
        }}
        afterChange={(slide, ref1) => {
          setIsMoving(false)
          let activeElements = carouselRef.current.listRef.current.querySelectorAll('.react-multi-carousel-item--active')

          activeElements.forEach((item, index) => {
            if (ref1.slidesToShow === 3) {
              if (index === 0) {
                item.classList.add(styles.itemCurrentPrev)
              }

              if (index === 1) {
                item.classList.add(styles.itemCurrent)
              }

              if (index === 2) {
                item.classList.add(styles.itemCurrentNext)
              }
            }

            if (ref1.slidesToShow === 1) {
              item.classList.add(styles.itemCurrent)
            }
          })
        }}
      >
        {
          items.map((item, index) => {
            return <div className={`${styles.item}`} key={index}>
                <img className={styles.img} src={item.img} alt='' draggable={false}/>
                <a className={styles.download} href={item.detail} target="_blank" rel="noopener noreferrer" download draggable={false} onClick={e => {
                  if (isMoving) {
                    e.preventDefault();
                  }
                }}>
                  <span className={styles.downloadIcon}>
                    <Download/>
                  </span>
                </a>
            </div>
          })
        }
      </MultiCarousel>
    </div>
  )
}
