import React from 'react'
import styles from './MobileMenu.module.css'

import { menu } from '../../data/menu'
import { ReactComponent as Rhombus } from '../../assets/svg/rhombus.svg'
import { ReactComponent as LogoRG } from '../../assets/svg/logo_rg.svg'
import Socials from '../Socials'

let chunkArray = (myArray, chunkSize) => {
  let index = 0
  let arrayLength = myArray.length
  let tempArray = []
  let myChunk

  for (index = 0; index < arrayLength; index += chunkSize) {
    myChunk = myArray.slice(index, index + chunkSize);
    tempArray.push(myChunk);
  }

  return tempArray;
}

export default function MobileMenu(props) {

  let getMenuItems = () => {
    let menuChunk = chunkArray(menu, 2)

    return <div className={styles.menu}>
      {
        menuChunk.map((menuChunkItem, index) => {
          return <div className={styles.menuSection} key={index}>
            {
              menuChunkItem.map((menuItem, chunkIndex) => {
                return <div className={`${styles.menuItemTitle} ${menuItem.active ? styles.menuItemTitleActive : ''}`} key={menuItem.title} onClick={(e) => {
                  props.chooseMobileItem && props.chooseMobileItem(menuItem, menuChunk, e)
                }}>
                  {menuItem.title}
                </div>
              })
            }
          </div>
        })
      }
    </div>
  }

  return (
    <div className={styles.root}>
      <div className={styles.rhombusRibbon}>
        <div className={styles.rhombusRibbonIcon}>
          <Rhombus/>
        </div>
      </div>
      {
        getMenuItems()
      }
      <div className={styles.rhombusRibbon}>
        <div className={styles.rhombusRibbonIcon}>
          <Rhombus/>
        </div>
      </div>
      <div className={styles.gotoRG}>
        <span className={styles.gotoRGtitle}>Новости</span>
        <a className={styles.gotoRGLink} rel="noopener noreferrer" href="//rg.ru" target="_blank">
          <LogoRG />
        </a>
      </div>
      <div className={styles.mobileSocial}>
        <Socials/>
      </div>
    </div>
  )
}