import React from 'react'
import styles from './PersonMenu.module.css'

import { floors } from '../../data/floors'

import { ReactComponent as Rhombus } from '../../assets/svg/rhombus.svg'

export default function PersonMenu(props) {
  const makeFloors = () => {
    return floors.map((floor, index) => {
      return <div
          className={styles.item}
          onClick={props.onClickPerson && props.onClickPerson.bind(this, floor)}
          key={floor.value + index}
        >
        <div className={styles.itemName}>{ floor.person.name } { floor.person.patronymic }</div>
        <div className={styles.itemSurname}>{ floor.person.surname }</div>
        <div className={styles.value}>
          <i className={styles.valueIndex}>{floor.value}</i>
        </div>
      </div>
    })
  }

  return (
    <div className={styles.root}>
      <div className={styles.rhombusRibbon}>
        <div className={styles.rhombusRibbonIcon}>
          <Rhombus/>
        </div>
      </div>
      { makeFloors() }
    </div>
  )
}
