import React, {useEffect, useState} from 'react'
import { useHistory } from 'react-router-dom'
import styles from './Main.module.css'

import { ReactComponent as Rhombus } from '../../assets/svg/rhombus.svg'
import { ReactComponent as Logo } from '../../assets/svg/logo.svg'
import { ReactComponent as Play } from '../../assets/svg/play.svg'
import { ReactComponent as Pause} from '../../assets/svg/pause.svg'


import manPicture from '../../assets/images/man.png'
import video from '../../assets/video/main_bg.mp4'

export default function Main() {
  const history = useHistory()
  const videoRef = React.createRef()

  const [isPlayVideo, setIsPlayVideo] = useState(false)

  // Handlers
  const enterHandler = () => {
    history.push('/hotel')
  }

  const playVideoHandler = () => {
    if (videoRef.current) {
      videoRef.current.play();
      setIsPlayVideo(true);
    }
  }

  const pauseVideoHandler = () => {
    if (videoRef.current) {
      videoRef.current.pause();
      setIsPlayVideo(false);
    }
  }

  useEffect(() => {
    if (videoRef.current) {
      videoRef.current.addEventListener('ended', () => {
        setIsPlayVideo(false);
      })
    }
  }, [videoRef, setIsPlayVideo])

  return (
    <div className={`${styles.root} ${isPlayVideo ? styles.rootIsPlayVideo : ''} ${!isPlayVideo ? styles.rootIsPauseVideo : ''}`}>
      <div className={styles.background}>
        <video className={styles.backgroundVideo} src={video} playsInline ref={videoRef} preload={'auto'}/>
      </div>
      <div className={styles.header}>
        <div className={styles.logo}>
          <Logo/>
        </div>
      </div>
      <div className={styles.content}>
        <div className={styles.contentInner}>
          <div className={styles.play} onClick={playVideoHandler}>
            <div className={styles.playIcon}>
              <Play/>
            </div>
          </div>
          <div className={styles.pause} onClick={pauseVideoHandler}>
            <div className={styles.pauseIcon}>
              <Pause/>
            </div>
          </div>
          <div className={styles.enter} onClick={enterHandler}>
            Зайти в&nbsp;гости
          </div>
          <div className={styles.rhombusBox}>
            <div className={styles.rhombusBoxIcon}>
              <Rhombus/>
            </div>
          </div>
          <div className={styles.gotoRG}>
            <a className={styles.gotoRGLink} rel="noopener noreferrer" href="//rg.ru" target="_blank">
              Перейти на&nbsp;RG.RU
            </a>
          </div>
        </div>
      </div>

      <img className={styles.manPicture} src={manPicture} alt=''/>
      <div className={styles.footer}/>
    </div>
  )
}